.banner {
	height: 180px;
	max-width: 500px;
	max-height: 180px;
	width: 500px;
	margin: 20px;
    background-image: url(./img/burnbanner.png);
    background-size: 500px;
    text-align: center;
    align-content: center;
}

.bannerGenerate {
    height: 180px;
	max-width: 500px;
	max-height: 180px;
	width: 500px;
    margin: 20px;
    margin-left:auto;
    margin-right:auto;
    background-image: url(./img/burnbanner.png);
    background-size: 500px;
    text-align: center;
    align-content: center;
}

